.NavParent {
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
}

.NavChild {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.3cm 5vw;
  /* border-bottom: thin solid rgb(53, 53, 53); */
}

.Menu {
  display: flex;
  flex-direction: row;
  column-gap: 0.4cm;
  align-items: center;
}

.LogoImg {
  width: 6cm;
  height: auto;
}
#overlayCont {
  display: none;
}
.overlayNavContainer {
  position: fixed;
  inset: 0;
  display: none;
  justify-content: flex-end;

  z-index: 5;
  backdrop-filter: blur(10px);
}

.overlayNav {
  background-color: rgb(37, 37, 37);
  border: 1px solid rgb(31, 31, 31);
  //   width: fit-content;
  display: flex;

  justify-content: center;
  // gap: 1cm;

  width: 6cm;
  padding: 1cm;
  height: 100%;
}

.olog {
  color: red;
}

.menuOverlay {
  display: flex;
  // justify-content: center;
  gap: 0.2cm;
  flex-direction: column;
}
.overlayitem {
  cursor: pointer;
  font-size: 18px;
  transition: 0.3s ease;

  &:hover {
    text-decoration: underline;
  }
}
.menuImg {
  width: 0.7cm;
  height: auto;
  cursor: pointer;
  display: none;
}

.itemLight {
  font-family: nunito sans;
  color: rgb(28, 28, 28);
  font-size: 16px;
  transition: 0.3s all ease;
  border-bottom: 2px solid transparent;
  text-decoration: none;
}

.itemLight:hover {
  /* color:rgb(180, 112, 253); */
  border-bottom: 2px solid rgb(171, 93, 254);
}
.Dark {
  color: rgb(238, 238, 238);
  font-size: 16px;
}

.noborder:hover {
  border-bottom: none;
}

.signOutBtn {
  width: 0.8cm;
  height: auto;
  cursor: pointer;
}

.active {
  /* color:rgb(171, 93, 254); */
  border-bottom: 2px solid rgb(171, 93, 254);
}

@media only screen and (max-width: 1000px) {
  .itemLight {
    font-size: 14px;
  }

  .LogoImg {
    width: 5cm;
  }

  .signOutBtn {
    width: 0.6cm;
  }
}

@media only screen and (max-width: 700px) {
  .itemLight {
    display: none;
  }

  .menuImg {
    display: flex;
  }
}
@media only screen and (max-width:600px){
    .menuOverlay {
       
        // justify-content: center;
        gap: 0.1cm;
       
      }
      .overlayitem{
        font-size: 17px;
      }
}
@media only screen and (max-width: 500px) {
  .LogoImg {
    width: 4.5cm;
  }
  .menuImg {
    width: 0.5cm;
  }

  .overlayNav {
 
    width: 100%;
   
  }

  .overlayitem{
    font-size: 16px;
  }
}

@media only screen and (max-width: 400px) {
  .LogoImg {
    width: 4cm;
  }
  .overlayitem{
    font-size: 15px;
  }
}

@media only screen and (max-width: 300px) {
  .LogoImg {
    width: 3.5cm;
  }

  .menuImg {
    width: 0.4cm;
  }
}
